// setup
import Eth from 'ethjs';
import ethUtil from 'ethjs-util';
import unit from 'ethjs-unit';
import * as config from '../constants/Constants';
import * as types from '../constants/ActionTypes';
import * as util from '../util';
const EthFilter = require('ethjs-filter');
var questionCoop = null;

const getPendingTransactions = (eth) => (dispatch) => {

  // get local pending
  var transactions = getTransactions(eth.address,'pending');

  const filters = new EthFilter(eth);
  window.web3.eth.filter("pending").watch(
      function(error,result){
          if (!error) {
              console.log("pending");
              console.log(result);
          }
      }
    )
}

const saveTransaction = (address, tx, key) => (dispatch) => {
  var txs = getTransactions();
  if (!txs) {
    txs = {};
  }

  if (!txs[address]) {
    txs[address] = {};
  }

  if (!txs[address][key]) {
    txs[address][key] = [];
  }

  var txObj = {};
  txObj[tx] = config.TransactionStatus.PENDING;

  txs[address][key].push(txObj);
  localStorage.setItem(config.TRANSACTIONS, JSON.stringify(txs));

  dispatch({
    type: types.TRANSACTION_UPDATE,
    transactions: txs[address],
  });

}

function getTransactions(address, filter) {
  var txs = localStorage.getItem(config.TRANSACTIONS);
  var response = [];
  try {
    response = JSON.parse(txs);
    if (address) {
      response = response[address];
    }
  } catch (e) {
    response = {};
  }
  return response;
}

export const appLoop = (eth) => (dispatch) => {
  getQuestionQueue(eth)(dispatch);
  getAnsweredQuestions(eth)(dispatch);
  getAskedQuestions(eth)(dispatch);
  getConfig(eth)(dispatch);
  getStats(eth)(dispatch);
  getPendingTransactions(eth);

  console.log("TX " + JSON.stringify(getTransactions()));
}

export const connectEth = () => (dispatch) => {
  var eth;
  // Checking if Web3 has been injected by the browser (Mist/MetaMask)
  if (typeof window.web3 !== 'undefined' && typeof window.web3.currentProvider !== 'undefined') {
    eth = new Eth(window.web3.currentProvider);
    //eth = new Eth(new Eth.HttpProvider('http://localhost:8545'));
  } else {
    eth = new Eth(new Eth.HttpProvider('http://localhost:8545'));
  }

  return dispatch({
    type: types.ETH_CONNECT,
    payload: eth
  });
}

export const initQuestionCoopContract = (eth) => (dispatch) => {
  var contractData = require('../data/QuestionCoop.json');
  questionCoop = eth.contract(contractData.abi).at(config.CONTRACT_ADDRESS);
  window.questionCoop = questionCoop;
  dispatch({
    type: types.CONTRACT_LOADED,
    address: config.CONTRACT_ADDRESS,
  });
};

function buildQuestion(questionId, question, meta) {

  var questionText = util.byteArrayToString(ethUtil, question[0]).trim().toString();
  // Adds a question mark if one is missing
  if (questionText.slice(-1) !== '?') {
    questionText = questionText + '?';
  }

  var q = {
    id: questionId,
    text: questionText,
    answer1: ethUtil.toAscii(question[1]),
    answer2: ethUtil.toAscii(question[2]),
    bounty: unit.fromWei(meta[0], 'ether'),
    maxAnswers: meta[1].toNumber(),
    totalAnswers: meta[2].toNumber(),
    state: meta[3].toNumber(),
    next: question[4],
  };
  return q;
}

async function getQuestion(questionId) {
  try {
    var questionObj = await questionCoop.getQuestion(questionId);
    var metaObj = await questionCoop.getQuestionMeta(questionId);
    var question = buildQuestion(questionId, questionObj, metaObj);
    if (question.state === 2) { // Complete
      var answer = await questionCoop.getWinningAnswerToQuestion(questionId);
      answer = !answer[0] ? question.answer1 : question.answer2;
      question.answer = answer;
    }
    return question;
  } catch (e) {
    console.log("ERROR" + e);
  }
}

async function buildQuestionQueue(cb) {
  var questions = [];
  questionCoop.activeQuestionHead()
    .then(async (head) => {
      var count = 0;
      var next = head[0];
      while (ethUtil.toUtf8(next) !== '' && count < (config.QUESTION_QUEUE_SIZE_MAX + 1)) {
        var q = await getQuestion(next);
        questions.push(q);
        if (q && q.next) {
          next = q.next;
        } else {
          break;
        }
        count++;
      }
      cb(questions);
    })
    .catch((e) => {
      cb(null, e);
    });
}

export const getQuestionQueue = () => (dispatch) => {
  buildQuestionQueue((questions, error) => {
    if (questions) {
      dispatch({
        type: types.QUESTIONS_GET_TOP_SUCCESS,
        questions,
      });
    } else {
      dispatch({
        type: types.QUESTIONS_GET_TOP_FAILURE,
        error,
      });
    }
  });
};

export const askQuestion = (eth, question, answer1, answer2, answers, bounty) => (dispatch) => {
  var questionBytes = util.stringToByteArray(ethUtil, question, 5);
  var answer1Bytes = ethUtil.fromAscii(answer1);
  var answer2Bytes = ethUtil.fromAscii(answer2);

  questionCoop.askQuestion(questionBytes, answer1Bytes, answer2Bytes, answers, {
    from: eth.address,
    gas: 3000000,
    value: unit.toWei(bounty, "ether")
  }, function(err, tx) {
    if (!err && tx) {
      saveTransaction(eth.address, tx, 'asked')(dispatch);
    }
  });
};

export const answerQuestion = (eth, questionId, answer) => (dispatch) => {
  questionCoop.answerQuestion(questionId, answer, {
      from: eth.address,
      gas: 1000000,
      gasPrice: unit.toWei(2, 'gwei')
    })
    .then((tx) => {
      saveTransaction(eth.address, tx, 'answered')(dispatch);
    })
    .catch((e) => {

    });
};

async function askedQuestions(from, cb) {
  try {
    var questions = [];
    var count = await questionCoop.askedQuestionCount(from);
    count = count[0].toNumber();

    for (var i = count - 1; i >= 0; i--) {
      var questionId = await questionCoop.askedQuestionAtIndex(from, i);
      questionId = questionId[0];
      var question = await getQuestion(questionId);
      questions.push(question);
    }
    cb(questions);
  } catch (e) {
    cb(null, e);
  }
}

export const getAskedQuestions = (eth) => (dispatch) => {
  askedQuestions(eth.address, (questions, error) => {
    if (!error) {
      dispatch({
        type: types.QUESTIONS_GET_ASKED_SUCCESS,
        questions,
      });
    } else {
      dispatch({
        type: types.QUESTIONS_GET_ASKED_FAILURE,
        error,
      });
    }
  });
};

async function answeredQuestions(from, cb) {
  try {
    var questions = [];
    var count = await questionCoop.getAnsweredQuestionCount(from);
    count = count[0].toNumber();

    for (var i = count - 1; i >= 0; i--) {
      var questionData = await questionCoop.getAnsweredQuestionByIndex(from, i);
      var questionId = questionData[0];
      var question = await getQuestion(questionId);
      var answer = !questionData[1] ? question.answer1 : question.answer2;
      question.myAnswer = answer;

      // Check cashout status
      var canCashOut = await questionCoop.canCashOut(questionId, {
        from
      });
      canCashOut = canCashOut[0];
      question.canCashOut = canCashOut;
      questions.push(question);
    }
    cb(questions);
  } catch (e) {
    cb(null, e);
  }
}

export const getAnsweredQuestions = (eth) => (dispatch) => {
  answeredQuestions(eth.address, (questions, error) => {
    if (!error) {
      dispatch({
        type: types.QUESTIONS_GET_ANSWERED_SUCCESS,
        questions,
      });
    } else {
      dispatch({
        type: types.QUESTIONS_GET_ANSWERED_FAILURE,
        error,
      });
    }
  });
};

export const cashout = (eth, question) => (dispatch) => {
  questionCoop.cashout(question.id, {
      from: eth.address
    },
    function(error, tx) {
      console.log(error, tx);
    });
}

async function getConfigAsync(cb) {
  var configData = {};
  questionCoop.maximumAnswersPerQuestion()
    .then(answers => {
      configData.maxAnswers = answers[0].toNumber();
      return questionCoop.minimumAnswersPerQuestion();
    })
    .then(answers => {
      configData.minAnswers = answers[0].toNumber();
      cb(configData);
    });
}

export const getConfig = () => (dispatch) => {
  getConfigAsync((c) => {
    dispatch({
      type: types.CONFIG_LOADED,
      config: c,
    });
  });
}

async function getStatsAsync(account, cb) {
  var statsData = {};
  questionCoop.getCashedOutBalance({
      from: account
    })
    .then(balance => {
      var balanceInEth = unit.fromWei(balance[0], 'ether');
      console.log('balance ' + balanceInEth);
      statsData = {
        cashedOutBalance: balanceInEth,
        address: account,
      };
      cb(statsData);
    })
    .catch(e => {
      console.log("err ");
      console.log(e);
      cb(null, e);
    });
}

export const getStats = (eth) => (dispatch) => {
  getStatsAsync(eth.address, function(statData, error) {
    dispatch({
      type: types.STATS_LOADED,
      ...statData,
    });
  });
}

export const getMaxAnswersByBounty = (bounty) => (dispatch) => {

  if (!bounty || parseFloat(bounty, 10) <= 0 || isNaN(parseFloat(bounty, 10))) {
    dispatch({
      type: types.CONFIG_MAX_ANSWERS_BY_BOUNTY,
      bounty: 0,
      maxAnswersByBounty: 0,
    });
    return;
  }
  var totalBounty = parseFloat(bounty, 10) - (parseFloat(bounty, 10) * .05);
  var votePrice = config.GAS_SPENT_TO_ANSWER_QUESTION_IN_GWEI * config.GAS_PRICE_TO_ANSWER_A_QUESTION;
  var minWinnings = votePrice * 2; // In gwei
  var gwei = unit.fromWei(unit.toWei(totalBounty, "ether"), "gwei");
  var maxAnswersByBounty = Math.floor(gwei / minWinnings);
  // Ensure the number is odd
  if (maxAnswersByBounty % 2 === 0) {
    maxAnswersByBounty -= 1;
  }
  dispatch({
    type: types.CONFIG_MAX_ANSWERS_BY_BOUNTY,
    bounty,
    maxAnswersByBounty,
  });
}

export const toggleQuestionModal = () => (dispatch) => {
  dispatch({
    type: types.TOGGLE_QUESTION_MODAL,
  });
};



