import React from "react";
import receiveIcon from "../assets/svgs/ic_receive.svg";
import walletIcon from "../assets/svgs/ic_wallet.svg";
import { Navbar, NavbarBrand, NavItem, Nav } from "reactstrap";

const NavStats = ({ user }) => {
  return (
    <Nav className="ml-auto" href="/">
      <NavItem>
        <img src={walletIcon} alt="Number of Answers" />{" "}
        {user.address ? user.address.slice(-5) : "Not logged in"}
      </NavItem>
      <NavItem className="pl-3">
        <img src={receiveIcon} alt="Ethereum Bounty" />{" "}
        {user.cashedOutBalance ? user.cashedOutBalance.slice(0, 6) : "0.00000"}
      </NavItem>
    </Nav>
  );
};

export default NavStats;



